import React from 'react';

// dentro de la pagina App.js se pueden poner componentes como <Route path="/about">
// pero dentro del componente mismo se reciven con el parentesis cuadrado esos elementos pasados parameticmrente

export const CommentsList = ({ comments }) => (

    <>
        <h3>Comments: ( {comments.length} )</h3>
        {comments.map(comment => (
            <div className="comment" key={comment.text}>
                <h4>{comment.postedBy}</h4>
                <h4>{comment.text}</h4>
            </div>

        ))}
    </>

);