import React, { useState, useEffect } from 'react';
import articles from './article-content'
import { useParams } from 'react-router-dom'
import { NotFoundPage } from './NotFoundPage';
import { CommentsList } from '../components/CommentsList'

export const ArticlePage = () => {
    const { name } = useParams();
    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] }); // esto define setArticleInfo
    const [nameValue, setNameValue] = useState('');
    const [commentValue, setCommentValue] = useState('');

    useEffect(() => {
        const fetchArticleInfo = async () => {
            const result = await fetch(`/api/articles/${name}`); //  se saca parte url despues de poner proxy en package.json
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchArticleInfo();

    }, [name]); // si no se pasa nada es que se va a cargar solo una vez cuando se carga el compoennte, se pueden poner gunciones de la pagina cuando se carga 
    // la idea es poner name pq si hay un link en la pagina que apunta a otro articulo no se actulizaran los nombres

    const article = articles.find(a => a.name === name)

    const upvoteArticle = async () => {
        const response = await fetch(`/api/articles/${article.name}/upvote`, {
            method: 'post',
        });
        const body = await response.json();
        setArticleInfo(body);
    };

    const addComment = async () => {
        const response = await fetch(`/api/articles/${article.name}/comment`, {
            method: 'post',
            body: JSON.stringify({ comment: { postedBy: nameValue, text: commentValue } }),
            headers: {
                'Content-Type': 'application/json'
            },
        });
        const body = await response.json();
        setArticleInfo(body);
    };

    return article ? (
        <>
            <h1>"{article.title}"</h1>
            <div id="upvotes-section">
                <p>This article has {articleInfo.upvotes} upvotes</p>
                <button onClick={upvoteArticle}>Add Upvote</button>


            </div>

            {article.content.map(paragraph => (

                <p key={paragraph[0]}>{paragraph}</p>
            )

            )}
            <div id="add-comment-form">

                <h3>Add a Comment</h3>
                <label>
                    Name:
                    <input type="text"
                        value={nameValue}
                        onChange={e => setNameValue(e.target.value)}></input>
                </label>
                <label>

                    Comment:
                    <textarea rows="4"
                        cols="50"
                        value={commentValue}
                        onChange={e => setCommentValue(e.target.value)} />
                </label>
                <button onClick={addComment}>Add Comment</button>
            </div>

            <CommentsList comments={articleInfo.comments} />
        </>
    ) : (
            <NotFoundPage />
        );
}